.dialogs {
  padding: 1em;
  display: grid;
  grid-template-columns: 20% 80%;
  grid-gap: 1em;
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.error {
  color: red;
  font-weight: 700;
}

.list__item a {
  display: block;
  font: 600 16px/2 'Arial';
  text-decoration: none;
  color: #0d0d0d;
}

.list__item .active {
  color: darkgoldenrod;
}

.list__item:nth-child(odd) {
  background: #fafafa;
}
